<template>
  <div class="container">
    <div class="section-header">
      <br />
      <br />
      <h2>상세정보</h2>
    </div>

    <div style="align: center,
    margin-left: 300px;">
      <div class="row marginbox">
        <h4 class="col-lg-4" align="center">아이디</h4>
        {{ Qna.id }}
      </div>
      <div class="row marginbox">
        <h4 class="col-lg-4" align="center">번호</h4>
        {{ Qna.no }}
      </div>
      <div class="row marginbox">
        <h4 class="col-lg-4" align="center">내용</h4>
        {{ Qna.contents }}
      </div>
      <div class="row marginbox">
        <h4 class="col-lg-4" align="center">등록시간</h4>
        {{ Qna.regdate }}
      </div>
    </div>

    <hr />

    <div align="center">
      <input
        v-model="newComment.comment"
        placeholder="새로운 댓글 등록"
        style="width: 750px; align-items: center;"
      />
      <br />
      <button @click="addComment(Qna.id, Qna.no)">등록</button>
    </div>
    <table class="table" align="center" style="width: 800px; align-items: center;">
      <tr align="center">
        <td width="100">작성자</td>
        <td width="150">내용</td>
        <td width="100">작성일</td>
        <td width="100">삭제</td>
      </tr>
      <template v-if="CommentLists[0]!=null">
        <tr v-for="comment in CommentLists" v-bind:key="comment.cno" align="center">
          <td width="100">{{ comment.id }}</td>
          <td width="150">{{ comment.comment }}</td>
          <td width="100">{{ comment.cregdate }}</td>
          <td width="100">
            <template v-if="LoginStatus.status==true && LoginStatus.id ==  comment.id">
              <button @click="deleteComment(comment.cno)">삭제</button>
            </template>
          </td>
        </tr>
      </template>
    </table>

    <div align="center">
      <input
        type="submit"
        id="searchButton"
        value="목록으로 돌아가기"
        class="btn btn-primary"
        @click="showList"
        style="
    margin-top: 23px;
"
      />
    </div>
  </div>
</template>

<script>
import Constant from "../Constant";
export default {
  name: "detailpage",
  data() {
    return {
      Qna: {},
      CommentLists: [],
      newComment: {
        qno: 0,
        comment: "",
        id: ""
      }
    };
  },
  computed: {
    LoginStatus() {
      return this.$store.state.loginStatus;
    }
  },
  mounted() {
    this.getComments();
    this.getQnalist();
  },
  created() {
    this.newComment.qno = this.$route.params.no;
    this.$store.dispatch(Constant.GET_LOGINSTATUS);
  },
  methods: {
    getComments() {
      console.log("댓글들을 불러옵니다.");
      this.$store
        .dispatch(Constant.GET_Comments, { qno: this.newComment.qno })
        .then(() => {
          this.CommentLists = { ...this.$store.state.CommentLists };
          console.log(this.CommentLists[0]);
        });
    },
    getQnalist() {
      this.$store
        .dispatch(Constant.GET_Qna, { no: this.$route.params.no })
        .then(() => {
          this.Qna = { ...this.$store.state.Qna };
        });
    },
    addComment(id, qno) {
      if (this.newComment.comment.trim() != "") {
        this.newComment.qno = qno;
        this.newComment.id = this.LoginStatus.id;
        console.log("id<<<<<" + qno);
        this.$store
          .dispatch(Constant.ADD_COMMENT, {
            qno: this.newComment.qno,
            id: this.newComment.id,
            comment: this.newComment.comment
          })
          .then(() => {
            this.getComments();
            this.clear();
          });
      }
    },
    deleteComment(no) {
      var cno = no;
      this.$store
        .dispatch(Constant.REMOVE_COMMENT, { cno: cno, qno: this.Qna.no })
        .then(() => {
          this.getComments();
        });
    },
    clear() {
      this.newComment.comment = "";
    },
    showList() {
      this.$router.push("/qna/list");
    }
  }
};
</script>

<style scoped></style>
